@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap);
*{box-sizing:border-box}body{margin:0;padding:0;font-family:'Poppins', sans-serif;background-color:#003679;color:white}

.nameInput{margin:20px;border-radius:4px;border:2px solid #9952e0;padding:10px;box-shadow:0 8px 16px 0 rgba(0,0,0,0.2);background-color:#003679;color:white;text-align:center;min-height:50px}.nameInput::-webkit-input-placeholder{background-color:#003679;color:grey;font-family:'Poppins', sans-serif;text-align:center}.nameInput:-ms-input-placeholder{background-color:#003679;color:grey;font-family:'Poppins', sans-serif;text-align:center}.nameInput::-ms-input-placeholder{background-color:#003679;color:grey;font-family:'Poppins', sans-serif;text-align:center}.nameInput::placeholder{background-color:#003679;color:grey;font-family:'Poppins', sans-serif;text-align:center}.nameInput:focus{outline:none;box-shadow:0 0 3pt 2pt #9952e0}

.playerCard{text-align:center;color:white;font-size:28px;display:flex;flex-direction:column;justify-content:center;align-items:center;margin-bottom:10px}.playerCard p{margin:10px 0}.playCard__icon-wrapper{display:flex;justify-content:center;align-items:center;width:200px;height:200px;border-radius:50%;border:6px double #52e0e0}.playCard__icon{color:#52e0e0}@media only screen and (max-width: 800px){.playerCard{font-size:14px;margin-bottom:10px}.playCard__icon-wrapper{display:flex;justify-content:center;align-items:center;width:120px;height:120px;border-radius:50%;border:6px double #52e0e0}}

.resultInfo{border:2px solid #99e052;border-radius:4px;padding:10px;color:white;font-size:24px;font-weight:500;display:flex;flex-direction:column;justify-content:center;align-items:center;text-align:center}.resultInfo p{margin:5px 0}@media only screen and (max-width: 1000px){.resultInfo{padding:3px;font-size:18px;margin-bottom:10px}.resultInfo p{margin:3px 0}}.resultInfo__text{color:#52e0e0}.win{color:#9952e0;font-weight:700}.lose{color:#e05252}

button{margin:15px 30px;padding:10px;width:120px;height:120px;border-radius:50%;background-color:#99e052;border:none;cursor:pointer;outline:none;transition:background-color 0.5s, box-shadow 0.2s}button:hover{background-color:#e05252}button:active{box-shadow:0 0 3pt 2pt #99e052}.small{width:60px;height:60px;padding:10px;background-color:#9952e0}.small:hover{background-color:#99e052}.small:active{box-shadow:0 0 3pt 2pt #9952e0}.button__icon{color:white}@media only screen and (max-width: 1000px){button{margin:10px;padding:10px;width:120px;height:120px}}@media only screen and (max-width: 800px){button{margin:10px;padding:10px;width:90px;height:90px}}

.gameOver{display:flex;flex-direction:column;justify-content:center;align-items:center}.gameOver__image-wrapper{width:100%;max-width:400px;border-radius:8px;margin-bottom:15px}.gameOver__image{width:100%;border-radius:8px}.gameOver__text{color:white;font-size:20px;font-weight:500;margin:15px 0}@media only screen and (max-width: 800px){.gameOver__text{font-size:18px;margin:20px 0}}

.game__heading{display:table;margin:3vh auto;font-size:48px;background:linear-gradient(330deg, #e05252 0%, #99e052 25%, #52e0e0 50%, #9952e0 75%, #e05252 100%);-webkit-background-clip:text;-webkit-text-fill-color:transparent}.game__text{text-align:center;color:white;font-size:20px;font-weight:500;margin:0;margin-bottom:5px}.red{color:#e05252}.input-wrapper{display:flex;justify-content:center;align-items:center}@media only screen and (max-width: 1100px){.game__heading{font-size:36px}.game__text{font-size:14px}}@media only screen and (max-width: 800px){.game__heading{font-size:24px}.game__text{font-size:14px}}

